@import url(https://fonts.googleapis.com/css?family=Lobster|Oleo+Script|Original+Surfer&display=swap);
/* eslint-disable-next-line */
body {
  background-color: #faf8d7;
  padding-left: 40px; }

.main-title {
  color: #d04764;
  font-family: Lobster, cursive;
  margin: 0 0 18px;
  text-shadow: 1px 0 0 #fff; }

.select-pages {
  cursor: pointer; }
  .select-pages__option {
    cursor: pointer; }

.tabs__title {
  color: #2ca4b0;
  font-family: "Oleo Script", cursive;
  font-size: 18px;
  line-height: 24px;
  margin: 0;
  text-shadow: 1px 0 0 #fff; }

.tabs__content {
  color: #333;
  font-family: "Original Surfer", cursive;
  font-size: 14px;
  line-height: 18px;
  margin: 0 0 24px; }

.paginator__button {
  cursor: pointer;
  border-color: #ececb8;
  background-color: none; }

